import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-youth-program',
  templateUrl: './youth-program.component.html',
  styleUrls: ['./youth-program.component.css']
})
export class YouthProgramComponent implements OnInit {
  days: string = '00';
  hours: string = '00';
  minutes: string = '00';
  seconds: string = '00';

  ngOnInit() {
    this.startCountdown();
  }

  startCountdown() {
    let dest = new Date("April 25, 2024 10:00:00").getTime();

    let x = setInterval(() => {
      let now = new Date().getTime();
      let diff = dest - now;

      if (diff <= 0) {
        clearInterval(x);
        this.days = '00';
        this.hours = '00';
        this.minutes = '00';
        this.seconds = '00';
        return;
      }

      let days = Math.floor(diff / (1000 * 60 * 60 * 24));
      let hours = Math.floor((diff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      let minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));
      let seconds = Math.floor((diff % (1000 * 60)) / 1000);

      // Format numbers to always have two digits
      this.days = days < 10 ? `0${days}` : `${days}`;
      this.hours = hours < 10 ? `0${hours}` : `${hours}`;
      this.minutes = minutes < 10 ? `0${minutes}` : `${minutes}`;
      this.seconds = seconds < 10 ? `0${seconds}` : `${seconds}`;

      // Get elements by class name
let countdownElements = document.getElementsByClassName("countdown-element");

// Loop through the elements and update their content
for (let i = 0; i < countdownElements.length; i++) {
let className = countdownElements[i].classList[1]; // Get the second class name
switch (className) {
  case "days":
    countdownElements[i].innerHTML = days.toString();
    break;
  case "hours":
    countdownElements[i].innerHTML = hours.toString();
    break;
  case "minutes":
    countdownElements[i].innerHTML = minutes.toString();
    break;
  case "seconds":
    countdownElements[i].innerHTML = seconds.toString();
    break;
  default:
    break;}}
    }, 1000);
  }
}
